<template>
  <form @submit.prevent="onSubmit" class="form">
    <div class="form__section">
      <TextField
        class="form__element"
        name="secondName"
        type="text"
        placeholder="Фамилия"
        :value="secondName"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="firstName"
        type="text"
        placeholder="Имя"
        :value="firstName"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="lastName"
        type="text"
        placeholder="Отчество"
        :value="lastName"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="dateBirth"
        type="date"
        placeholder="Дата рождения:"
        :value="dateBirth"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <RadioInput name="gender" :options="genders" @updateField="updateField" />
    </div>
    <div class="form__section">
      <TextField
        class="form__element"
        name="phoneNumber"
        type="text"
        placeholder="Номер телефона"
        :value="phoneNumber"
        @updateField="updateField"
      />
    </div>
    <div class="form__section form__section_address">
      <TextField
        class="form__element"
        name="index"
        type="text"
        placeholder="Индекс"
        :value="index"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="country"
        type="text"
        placeholder="Страна"
        :value="country"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="region"
        type="text"
        placeholder="Область"
        :value="region"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="city"
        type="text"
        placeholder="Город"
        :value="city"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="street"
        type="text"
        placeholder="Улица"
        :value="street"
        @updateField="updateField"
      />
      <TextField
        class="form__element"
        name="house"
        type="text"
        placeholder="Дом"
        :value="house"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <p class="form__banner">Группа пациента</p>
      <MultiSelectorInput
        class="form__element"
        name="customerGroup"
        placeholder="Выберите свою(-и) группу(-ы)"
        :options="customerGroups"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <p class="form__banner">Лечащий врач</p>
      <SelectorInput
        class="form__element"
        name="doctor"
        text="Выберите лечащего врача"
        :options="doctors"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <CheckboxInput
        class="form__element"
        name="noSentSMS"
        :text="sendSMS.text"
        :isChecked="sendSMS.isChecked"
        @updateField="updateField"
      />
    </div>
    <p>
      {{ firstName }} {{ secondName }} {{ lastName }} {{ dateBirth }}
      {{ phoneNumber }} {{ gender }} {{ noSentSMS }} {{ customerGroup }}
      {{ doctor }}
    </p>
  </form>
</template>

<script>
import TextField from './TextField.vue';
import RadioInput from './RadioInput.vue';
import SelectorInput from './SelectorInput.vue';
import MultiSelectorInput from './MultiSelectorInput.vue';
import CheckboxInput from './CheckboxInput.vue';

export default {
  name: 'Form',
  components: {
    TextField,
    RadioInput,
    SelectorInput,
    MultiSelectorInput,
    CheckboxInput,
  },
  data() {
    return {
      // Отправляемые данные
      firstName: '',
      secondName: '',
      lastName: '',
      dateBirth: '',
      phoneNumber: '',
      gender: '',
      noSentSMS: false,
      customerGroup: [],
      doctor: '',
      index: '',
      country: '',
      region: '',
      city: '',
      street: '',
      house: '',
      // Данные для заполнения
      genders: [
        { value: 'Male', text: 'Мужчина' },
        { value: 'Female', text: 'Женщина' },
      ],
      customerGroups: ['VIP', 'Проблемные', 'ОМС'],
      doctors: ['Иванов', 'Захаров', 'Чернышева'],
      sendSMS: { isChecked: false, text: 'Не отправлять СМС' },
    };
  },
  methods: {
    updateField(data) {
      this[data.key] = data.value;
    },
    onSubmit() {},
  },
};
</script>

<style scoped lang="sass">
.form
  display: flex
  flex-direction: column
  font-size: 18px
  padding: 15px
  background: white
  border: 1px solid rgba(0, 0, 0, 0.12)
  box-sizing: border-box
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2)
  border-radius: 4px

.form__section
  display: flex
  flex-direction: column
  margin-bottom: 10px
  &_address
    margin-bottom: 0px
    flex-direction: row
    flex-wrap: wrap
    justify-content: space-between
    & .form__element
      margin-bottom: 10px
    & .form__element:nth-child(1),
    & .form__element:nth-child(3),
    & .form__element:nth-child(5)
      margin-right: 5px
    & .form__element:nth-child(2),
    & .form__element:nth-child(4),
    & .form__element:nth-child(6)
      margin-left: 5px

.form__banner
  margin: 0
  line-height: 30px

.form__element
  flex-grow: 1
</style>
